@import "./variables";
html{
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  // background: #f4f5f7;
}
.header{
  background-color: hsla(0,0%,100%,.4);
  backdrop-filter: blur(20px);
  border-style: solid;
  border-color: #E7EBF0;
  border-width: 0;
  border-bottom-width: thin;
  height: 48px;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;left: 0;
  width: 100%;
  h1{
    color:#152a41;
    font-size: 24px;
    line-height: 48px;
    transition: all ease .2s;
    cursor: pointer;
    &:hover{
      color:$purple-color;
    }
  }
}

main{
  
  min-height: 100vh;
  flex:1 1 auto;
  font-size: 16px;

  section{
    min-height: 100vh;
  }
  h1{
    font-size: 44px;
    line-height: 54px;
    color: $purple-color;
    
  }
  h2{
    font-size: 22px;
    color: $purple-light-color;
    margin: 0;
  }
  img{
    margin-top: 40px;
  }
}
.homescreen{
  background: url("./../assets/background.svg") center/cover no-repeat;
}
.mainBtn{
  margin-top: 40px;
}
.btn{
    border: 2px solid $purple-color;
    color:$purple-color;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
   
    svg{
      position: relative;
      left: 8px;
      transition: all ease-in-out .3s;
      path{
        fill:$purple-color;
      }
    }
    
    &:hover{
      background-color: $purple-color;
      color:#fff;
      svg{
        transform: translate(5px);
        path{
          fill: #fff;
        }
        
      }
    }
    text-decoration: none;
    cursor: pointer;
    outline: none;
    transition-property: background-color,border-color;
    transition-duration: .3s;
    transition-timing-function: ease;
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    border-radius: 100px;
    padding: 0 32px;
    white-space: nowrap;
    
}
.container{
  display: flex;
  max-width: 1440px;
  margin: 0 15px;
  width: 100%;
  
}
.about{
  background-color: #fff;
  height: 100%;
  padding-bottom: 80px;
  box-shadow:inset 0px 2px 30px rgb(21 42 65 / 5%);
  display: flex;
  flex-direction: column;
  align-items: center;
  &__items{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: nowrap;
    margin-top: 50px;
  }
  &__item{
    border-radius: 20px;
    max-width: 370px;
    flex: 1 1;
    margin:0 15px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    transition: all ease .2s;
    &:hover{
      box-shadow:0px 2px 30px rgb(21 42 65 / 10%);
      transform: scale(1.01);
    }
    h4{
      font-weight: 500;
      color:$dark-color;
      font-size: 22px;
      line-height: 30px;
      margin: 10px 0;
      margin-top: 25px;
    }
    p{
      flex:1 1 auto;
      font-size: 16px;
      font-weight: 400;
      color: $light-color;
      line-height: 150%;
      max-width: 370px;
    }
  }
  h1{
    padding-top: 60px;
    color:$dark-color;
    margin-bottom: 20px;
  }
  .btn{
    
    align-self: flex-start;
    border:none;
    font-weight: bold;
    font-size: 16px;
    padding-left: 0;
    text-decoration:none;
    &:hover{
      background-color: transparent;
      color: $purple-color;
      svg{
        
        position: relative;
        left: 8px;
        transition: all ease-in-out .3s;
        path{
          fill:$purple-color;
        }
      }
    }
  }
}