@import "./../../scss/variables";
.portfolio{
    box-shadow:inset 0px 2px 30px rgb(21 42 65 / 5%);
    height: 100%;
    width: 100%;
    &__wrap{
        width: 100%;
        margin: 0 40px;
    }
    h1{
        margin-top: 120px;
    }
    background-color: $light-background-color;
    .teacherCard{
        display: flex;
        background-color: #fff;
        border-radius: 20px;
        width: 100%;
        padding: 10px;
        margin:40px 0;
        &__image{
            &--wrap{
                align-self: center;
                justify-self: center;
                min-width: 200px!important;
                height: 200px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            
            width: 150px;
            height: 150px;
            margin: 0;
            border-radius: 20px;
        }
        &__info{
            min-width: 370px;
            max-width:370px;
            margin-left: 20px;
            border-radius: 20px;
            padding:5px 15px;
            background-color: $light-background-color;
            color:$dark-color;
            h3{
                
                font-weight: 500;
                font-size: 22px;
                line-height: 30px;
                margin: 10px 0;
            }
            p{
                font-size: 16px;
                font-weight: 500;
                line-height: 150%;
            }
            
        }
    }
}
.teacherCard--special{
    width: 100%;
    max-width: none!important;
    display: flex;
    flex-direction: column;
    p{
        flex:1 1 auto;
    }
    .btn{
        align-self: flex-end;
        justify-content: flex-end;
        margin: 10px;
    }
}